


































const Glide= require('vue-glide-js')
const GlideSlide= require('vue-glide-js')
import { Component, Vue } from 'vue-property-decorator'

interface Options {
  gap :Number,
  type: string,
  autoplay: number,
  rewind: boolean,
  bound: boolean,
  perView?: number,
  breakpoints?: {},
}

@Component({
  components: {
    [Glide.name]: Glide,
    [GlideSlide.name]: GlideSlide
  }}) 

export default class App extends Vue {
  private srcArr: Array<string> = ["drapak2.jpg","drapak3.jpg","drapak5.jpg", "drapak8.jpg", "drapak6.jpg", "drapak7.jpg", "drapak9.jpg", "drapak10.jpg" ]
  private windowWidth: number = 0;
  private numOfImg: number = 1;
  private options: Options = {
    gap: 0,
    type: 'carousel',
    autoplay: 4000,
    rewind: true,
    bound: true,
    breakpoints: {
      1024: {
        perView: 1,
      }, 
    }
  }
  mounted() {
    this.$nextTick(function() {
      window.addEventListener('resize', this.getWindowWidth);
      this.getWindowWidth()
    })
  }
  beforeDestroy() {
    window.removeEventListener('resize', this.getWindowWidth);
  }
  public getWindowWidth() {
    this.windowWidth = document.documentElement.clientWidth;

    if(this.windowWidth<=1000) {
      this.options.perView=1;
    }
    else if(this.windowWidth>1000) {     
      this.options.perView=3;
    }
  }
}
